.load {
  margin-top: 15%;
  font-family: "ZCOOL KuaiLe", cursive;
  font-size: 40px;
  color: palevioletred;
  text-align: center;
  &:after {
    content: "\2026";
    display: inline-block;
    overflow: hidden;
    vertical-align: bottom;
    animation: dots steps(4, end) 2s infinite;
    width: 0px;
  }
}

@keyframes dots {
  to {
    width: 1.25em;
  }
}
