@import url(https://fonts.googleapis.com/css2?family=Baloo+Da+2:wght@600&family=Merriweather:wght@300&display=swap);
a{padding:2px;text-decoration:none;color:#000}a:hover{border-bottom:2px solid #000}a.active{border-bottom:2px solid #000}header{position:-webkit-sticky;position:sticky;background:#e1e1e1;top:10px;width:100%;display:flex;align-items:center;margin:0 auto;padding:10px 20px;border-radius:5px;box-shadow:3px 3px 5px rgba(0,0,0,0.4);text-shadow:2px 2px 2px rgba(0,0,0,0.3)}header h1{font-family:"Baloo Da 2", cursive}header ul{margin-left:2em;display:flex;align-items:center}header ul li{margin-left:2em}header ul li:first-child{margin-left:0}header ul li a{color:#2cce4f;text-transform:uppercase}header ul li a:hover{border-bottom:2px solid #2cce4f}header ul li a.active{border-bottom:2px solid #2cce4f}header a{margin-left:auto}header .hamburger{display:none}@media screen and (max-width: 768px){header .hamburger{width:50px;height:50px;border:3px solid #000;display:flex;flex-direction:column;align-items:center;justify-content:space-around}header .hamburger .bar{width:40px;height:4px;background-color:#000;position:relative}header .hamburger .bar::before{content:"";display:block;position:absolute;top:10px;width:40px;height:4px;background-color:#000}header .hamburger .bar::after{content:"";display:block;position:absolute;bottom:10px;width:40px;height:4px;background-color:#000}}

.load{margin-top:15%;font-family:"ZCOOL KuaiLe", cursive;font-size:40px;color:palevioletred;text-align:center}.load:after{content:"\2026";display:inline-block;overflow:hidden;vertical-align:bottom;-webkit-animation:dots steps(4, end) 2s infinite;animation:dots steps(4, end) 2s infinite;width:0px}@-webkit-keyframes dots{to{width:1.25em}}@keyframes dots{to{width:1.25em}}

*{padding:0;margin:0;box-sizing:border-box;list-style:none}body{background:#e1e1e1}.App{max-width:1400px;width:100vw;height:100vh;margin:0 auto;padding:20px;font-family:"Merriweather", serif}

